<template>
  <v-menu
    bottom
    left
    offset-y
    origin="top right"
    transition="scale-transition"
  >
    <template v-slot:activator="{ attrs, on }">
      <v-btn
        class="ml-2"
        min-width="0"
        text
        v-bind="attrs"
        v-on="on"
      >
        <v-badge
          v-if="profile != 'CUSTOMER' && allExpired.length != 0"
          bordered
          color="red"
          overlap
        >
          <template v-slot:badge>
            <span>{{ allExpired.length }}</span>
          </template>
          <v-icon>mdi-bell</v-icon>
        </v-badge>
        <v-badge
          v-else-if="profile === 'CUSTOMER' && customerExpired.length != 0"
          bordered
          color="red"
          overlap
        >
          <template v-slot:badge>
            <span>{{ customerExpired.length }}</span>
          </template>
          <v-icon>mdi-bell</v-icon>
        </v-badge>
        <v-icon v-else>
          mdi-bell
        </v-icon>
      </v-btn>
    </template>

    <v-list
      v-if="profile != 'CUSTOMER' && allExpired.length != 0"
      flat
      nav
      dense
    >
      <app-bar-item
        v-for="(n, i) in allExpired"
        :key="i"
        disabled
      >
        <v-list-item-content>
          <v-list-item-title>{{ n }}</v-list-item-title>
        </v-list-item-content>
      </app-bar-item>
    </v-list>
    <v-list
      v-else-if="profile === 'CUSTOMER' && customerExpired.length != 0"
      flat
      nav
      dense
    >
      <app-bar-item
        v-for="(n, i) in customerExpired"
        :key="i"
        disabled
      >
        {{ n }}
      </app-bar-item>
    </v-list>

    <v-list
      v-else
      disabled
    >
      <app-bar-item>Não há notificações</app-bar-item>
    </v-list>
  </v-menu>
</template>

<script>
  import AppBarItem from '@/components/app/BarItem'

  export default {
    name: 'SoonExpirationNotification',

    components: { AppBarItem },

    data: () => ({
      customerExpired: [],
      allExpired: [],
    }),

    computed: {
      profile () {
        return window.localStorage.profile
      },
      razaoSocial () {
        return window.localStorage.razaosocial
      }
    },

    created () {
      this.fetchExpired()
    },

    methods: {
      fetchExpired () {
        return fetch('https://sheetdb.io/api/v1/afxq0nl1f0gvg')
          .then((res) => res.json())
          .then((res) => {
            let allExp = res.filter(r => r.STATUSAET.includes('Vence em '))
            let customerExp = res.filter(r => (r.EMPRESA === this.razaoSocial && r.STATUSAET.includes('Vence em ')))

            allExp.forEach(solic => this.allExpired.push(solic.EMPRESA + ' - Licença ID ' + solic.ID + ' ' + solic.STATUSAET))
            customerExp.forEach(solic => this.customerExpired.push('AET ' + solic.AET + ' ' + solic.STATUSAET))
          }
        )
      },
    },
  }
</script>
